<template>
  <div>
    <div class="top">
      <div class="left">
        <img src="https://netflixrank.com/public/upload/1663/15968592331596788833whatif2_1o.jpg">
      </div>
      <div class="right">
         <img src="https://occ-0-3594-2219.1.nflxso.net/dnm/api/v6/tx1O544a9T7n8Z_G12qaboulQQE/AAAABYjGhBffDK1oNpIwDKkBfCyQSwlJDVZaWII11_b3mBlj5ltDMECud-Jwe_y0BrxKmXO2XGQM0yqX8fRM1IAy0jlWNY7UZpyrMNde2n_x5cjxrlglIQs_AnIf6EQ9QXuwBh05EoaH-aUu9Uot4UcRrY2GBiJxTM8GynzVk9VufIjcyA.png?r=07c">
         <h2>如／果</h2>
         <span>2019 | <span style="border: 1px solid grey; padding: 0 5px">16+</span> | 1 季 | 驚悚節目</span>
         <p>本片評分：</p><b-form-rating
          v-model="value2"
          variant="danger"
          readonly
          show-value
          show-value-max
          precision="1">
          </b-form-rating>
          <el-tabs v-model="activeName" type="card">
            <el-tab-pane label="簡介" name="first">一位抱持理想主義的科學家急於為她的醫療科技新創公司取得資金，於是她和丈夫與神祕的投資人簽下令人咋舌的協議。</el-tab-pane>
            <el-tab-pane label="主演" name="second">芮妮·齊薇格，珍·李薇，布雷克·詹納</el-tab-pane>
          </el-tabs>
      </div>
    </div>
    <div class="wrap">
      <h1>各集簡介</h1>
        <b-tabs content-class="mt-3" small>
            <b-tab title="第一季" active>
              <div class="content">
                <div class="image" v-for="(img, index) in imgs" :key="index">
                  <img :src="img.img">
                  <p>第{{img.id}}集</p>
                  <span>{{img.summary}}</span>
                </div> 
              </div>
            </b-tab>
        </b-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      value2: 3.5,
      activeName: 'first',
      imgs: []
    }
  },
  created(){
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs = res.data.if;
        })
    }
  
}
</script>

<style scoped>
*{
  color: #FFF;
}
.top{
  width: 100%;
  padding: 3% 10%;
  display: flex;
  margin: 0 auto;
  background: linear-gradient(to right, #000,rgba(0,0,0,0.5)),url('https://news.agentm.tw/wp-content/uploads/what-if_still_1-750x423.jpg') no-repeat scroll right;
  background-size: contain;
}
.left{
  flex-basis: 40%;
}
.left img{
  width: 400px;
}
.right{
  flex-basis: 60%;
}
.right img{
  width: 60%;
  margin-bottom: 2rem;
}
.right span{
  font-size: 0.9rem;
  letter-spacing: 1px;
  line-height: 2rem;
  color: grey;
}
.right p{
  line-height: 2rem;
}
.b-rating{
  width: 31%;
  color: #000;
  margin-bottom: 3rem;
}
.el-tabs{
  width: 70%;
}

.wrap{
  width: 100%;
  background: rgb(22, 22, 22);
  padding: 2rem 3rem;
}

.content{
  width: 100%;
  padding: 1rem 0;
  margin: 0 auto;

}
.image{
  width: 22.5%;
  min-height: 320px;
  display: inline-block;
  margin: 1rem 1rem 0 1rem;
  vertical-align: super;
}
.image img{
  width: 100%;
  margin-bottom: 1rem;
}
.image span{
  color: #FFF;
  font-size: 0.7rem;
}

@media only screen and (max-width: 450px){
.top{
  width: 100%;
  padding: 0 5%;
  flex-direction: column;
  background: #000;
}
.left{
  flex-basis: 90%;
  margin-bottom: 2rem;
}
.left img{
  width: 100%;
}
.right{
  margin-left: 2rem;
  padding-bottom: 3rem;
}
.right img{
  display: none;
}
.b-rating{
  width: 70%;
}
.el-tabs{
  width: 90%;
}
.wrap{
  padding: 2rem 1rem;
}
.image{
  width: 100%;
  margin: 2rem auto;
}
.content h1, .content h3{
  margin-left: 0;
}
}
</style>


