<template>
  <div>
    <div class="top">
      <div class="left">
        <img src="https://netflixrank.com/public/upload/6847/1602259345Emily-in-Paris.jpg">
      </div>
      <div class="right">
         <img src="https://occ-0-3594-2219.1.nflxso.net/dnm/api/v6/tx1O544a9T7n8Z_G12qaboulQQE/AAAABTcn3OBdyrHAP6qYxDMHtdKLQ6xq7XhOQQs07DhEkNx3rTLGJBqbFL52s5rvtZNGVqQyzUi2aM-ZKb1JfMuN5QcQHm97CFFw-09CNNTYZjR9aOKMkMjO-ISMHjBFrxTqBwRClQxThhoz1qFshs7vBZs9VTE88-E_Nx88Ef5BEv0ryA.png?r=231">
         <h2>艾蜜莉在巴黎</h2>
         <span>2020 | <span style="border: 1px solid grey; padding: 0 5px">13+</span> | 1 季 | 喜劇節目</span>
         <p>本片評分：</p><b-form-rating
          v-model="value2"
          variant="danger"
          readonly
          show-value
          show-value-max
          precision="1">
          </b-form-rating>
          <el-tabs v-model="activeName" type="card">
            <el-tab-pane label="簡介" name="first">芝加哥行銷主管艾蜜莉·庫柏獲得一份在巴黎的夢幻工作，看她勇於面對新生活的種種挑戰，努力兼顧事業、友誼與愛情。</el-tab-pane>
            <el-tab-pane label="主演" name="second">莉莉·柯林斯，菲莉帕妮·萊蘿伊柏莉，艾許莉·朴</el-tab-pane>
          </el-tabs>
      </div>
    </div>
    <div class="wrap">
      <h1>各集簡介</h1>
      <b-tabs content-class="mt-3" small>
        <b-tab title="第一季" active>
          <div class="content">
            <div class="image" v-for="(img, index) in imgs" :key="index">
              <img :src="img.img">
              <p>第{{img.id}}集</p>
              <span>{{img.summary}}</span>
            </div> 
          </div>
        </b-tab>
    </b-tabs>
      
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      value2: 4,
      activeName: 'first',
      imgs: [],
    }
  },
  created(){
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs = res.data.paris;
        })
    }
  
}
</script>

<style scoped>
*{
  color: #FFF;
}
.top{
  width: 100%;
  padding: 3% 10%;
  display: flex;
  margin: 0 auto;
  background: linear-gradient(to right, #000,rgba(0,0,0,0.6)),url('https://www.dramaqueen.com.tw/images/news/2020071411461897040e6a90a48cddfffd3ee3d4b5e9559549.jpg') no-repeat scroll right;
  background-size: contain;
}
.left{
  flex-basis: 40%;
}
.left img{
  width: 400px;
}
.right{
  flex-basis: 60%;
}
.right img{
  width: 60%;
  margin-bottom: 2rem;
}
.right span{
  font-size: 0.9rem;
  letter-spacing: 1px;
  line-height: 2rem;
  color: grey;
}
.right p{
  line-height: 2rem;
}
.b-rating{
  width: 31%;
  color: #000;
  margin-bottom: 3rem;
}
.el-tabs{
  width: 70%;
}

.wrap{
  width: 100%;
  background: rgb(22, 22, 22);
  padding: 2rem 3rem;
}

.content{
  width: 100%;
  padding: 1rem 0;
  margin: 0 auto;

}
.image{
  width: 22.5%;
  min-height: 320px;
  display: inline-block;
  margin: 1rem 1rem 0 1rem;
  vertical-align: super;
}
.image img{
  width: 100%;
  margin-bottom: 1rem;
}
.image span{
  color: #FFF;
  font-size: 0.7rem;
}

@media only screen and (max-width: 450px){
.top{
  width: 100%;
  padding: 0 5%;
  flex-direction: column;
  background: #000;
}
.left{
  flex-basis: 90%;
  margin-bottom: 2rem;
}
.left img{
  width: 100%;
}
.right{
  margin-left: 2rem;
  padding-bottom: 3rem;
}
.right img{
  display: none;
}
.b-rating{
  width: 70%;
}
.el-tabs{
  width: 90%;
}
.wrap{
  padding: 2rem 1rem;
}
.image{
  width: 100%;
  margin: 2rem auto;
}
.content h1, .content h3{
  margin-left: 0;
}
}
</style>


