<template>
<div class="wrap">
  <b-carousel
    id="carousel-fade"
    style="text-shadow: 0px 0px 2px #000"
    :interval="3000"
    fade
    indicators
    img-width="1"
    img-height="1"
  >
    <b-carousel-slide v-for="(img, index) in imgs" :key="index"><template v-slot:img>
          <img
            class="d-block img-fluid w-100"
            width="1"
            height="1"
            :src="img.img"
            alt="image slot"
          >
        </template></b-carousel-slide>
  </b-carousel>
</div>
</template>

<script>
export default {
    name: 'carousel',
    data(){
        return{
            imgs: []
        }
    },
    created(){
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs = res.data.messages;
        })
    }
}
</script>

<style scoped>

.wrap{
  width: 100%;
  padding: 1rem 0;
  background: linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.8)),url('https://images.unsplash.com/photo-1534375971785-5c1826f739d8?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1550&q=80') no-repeat scroll center;
}
.carousel{
  width: 30%;
  margin: 0 auto;
}
img{
  max-height: 674px;
}
@media only screen and (max-width: 450px){
.wrap{
  background: transparent;
}
.carousel{
  width: 100%;
}
}
</style>


