<template>
  <div>
    <div class="top">
      <div class="left">
        <img src="https://netflixrank.com/public/upload/5889/15967061722018050910491316354ec091902f9d09994d874b74675f6273.jpg">
      </div>
      <div class="right">
         <img src="https://occ-0-3594-2219.1.nflxso.net/dnm/api/v6/tx1O544a9T7n8Z_G12qaboulQQE/AAAABVMy_B_P6iJh8C2ZD6M3FcYHP5mhrMjAzvYr-8F2KoFTWIZXHPJTK0E8oaROSaOtrpK2KBCvNtZCfJEFH_sch9LfvmhlRk-VcqolGa83h7ukqHmXuQAglYT2Tvh8FHKP7HK2l9jbZDmwgpRW_xJJVf1NJD2BCrR_-vtaJm6UuGkmgA.png?r=d93">
         <h2>漫威盧克凱奇</h2>
         <span>2016 | <span style="border: 1px solid grey; padding: 0 5px">16+</span> | 共 2 季 | 犯罪電視戲劇</span>
         <p>本片評分：</p><b-form-rating
          v-model="value2"
          variant="danger"
          readonly
          show-value
          show-value-max
          precision="1">
          </b-form-rating>
          <el-tabs v-model="activeName" type="card">
            <el-tab-pane label="簡介" name="first">身穿連帽衫，堅不可摧的前科犯努力洗刷污名，並拯救社區。他不打算參與戰鬥，但大家都需要個英雄。</el-tab-pane>
            <el-tab-pane label="主演" name="second">邁克·科特（Mike Colter），阿爾弗·伍德德（Alfre Woodard），羅薩里奧·道森（Rosario Dawson），西蒙妮·米西克（Simone Missick），馬哈莎拉·阿里（Mahershala Ali），埃里克·拉雷·哈維（Erik LaRay Harvey），西奧·羅西（Theo Rossi），弗蘭克·惠利（Frank Whaley），弗蘭基·菲森（Frankie Faison），索尼亞·布拉加（Sonia Braga），羅恩·塞弗斯·瓊斯（Ron Cephas Jones）</el-tab-pane>
          </el-tabs>
      </div>
    </div>
    <div class="wrap">
      <h1>各集簡介</h1>
        <b-tabs content-class="mt-3" small>
            <b-tab title="第一季" active>
              <div class="content">
                <div class="image" v-for="(img, index) in imgs" :key="index">
                  <img :src="img.img">
                  <p>第{{img.id}}集</p>
                  <span>{{img.summary}}</span>
                </div> 
              </div>
            </b-tab>
            <b-tab title="第二季">
              <div class="content">
                <div class="image" v-for="(img, index) in imgs2" :key="index">
                  <img :src="img.img">
                  <p>第{{img.id}}集</p>
                  <span>{{img.summary}}</span>
                </div> 
              </div>
            </b-tab>
        </b-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      value2: 4,
      activeName: 'first',
      imgs: [],
      imgs2: []
    }
  },
  created(){
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs = res.data.luke;
        })
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs2 = res.data.luke2;
        })
    }
  
}
</script>

<style scoped>
*{
  color: #FFF;
}
.top{
  width: 100%;
  padding: 3% 10%;
  display: flex;
  margin: 0 auto;
  background: linear-gradient(to right, #000,rgba(0,0,0,0.5)),url('https://m.media-amazon.com/images/M/MV5BMTUyMzgxMjQ0M15BMl5BanBnXkFtZTgwNDAwMjcxMDI@._V1_FMjpg_UX1280_.jpg') no-repeat scroll right;
  background-size: contain;
}
.left{
  flex-basis: 40%;
}
.left img{
  width: 400px;
}
.right{
  flex-basis: 60%;
}
.right img{
  width: 60%;
  margin-bottom: 2rem;
}
.right span{
  font-size: 0.9rem;
  letter-spacing: 1px;
  line-height: 2rem;
  color: grey;
}
.right p{
  line-height: 2rem;
}
.b-rating{
  width: 31%;
  color: #000;
  margin-bottom: 3rem;
}
.el-tabs{
  width: 70%;
}

.wrap{
  width: 100%;
  background: rgb(22, 22, 22);
  padding: 2rem 3rem;
}

.content{
  width: 100%;
  padding: 1rem 0;
  margin: 0 auto;

}
.image{
  width: 22.5%;
  min-height: 320px;
  display: inline-block;
  margin: 1rem 1rem 0 1rem;
  vertical-align: super;
}
.image img{
  width: 100%;
  margin-bottom: 1rem;
}
.image span{
  color: #FFF;
  font-size: 0.7rem;
}

@media only screen and (max-width: 450px){
.top{
  width: 100%;
  padding: 0 5%;
  flex-direction: column;
  background: #000;
}
.left{
  flex-basis: 90%;
  margin-bottom: 2rem;
}
.left img{
  width: 100%;
}
.right{
  margin-left: 2rem;
  padding-bottom: 3rem;
}
.right img{
  display: none;
}
.b-rating{
  width: 70%;
}
.el-tabs{
  width: 90%;
}
.wrap{
  padding: 2rem 1rem;
}
.image{
  width: 100%;
  margin: 2rem auto;
}
.content h1, .content h3{
  margin-left: 0;
}
}
</style>


