<template>
  <div>
    <div class="top">
      <div class="left">
        <img src="https://netflixrank.com/public/upload/5913/15967648698b8e9d48307cad017c7a6166188e8b0c.jpg">
      </div>
      <div class="right">
         <img src="https://occ-0-3594-2219.1.nflxso.net/dnm/api/v6/tx1O544a9T7n8Z_G12qaboulQQE/AAAABTVgzYIHJD19yMF5_TYDD_FUrIkCVpRkiVktutdw8HQMUl4EAJkfMZ3Tg1y3-0QjXHaCF7gMWfqtFZy9ZXqigo1Ixs02m9JEfGt0aixTG5kT0t5kpTblZG_6xnHY7r0j9LTobsE1cVALpzs4avjPeRP2SjIASFDhTHzw3ZsO_VMkBg.png?r=51e">
         <h2>如／果</h2>
         <span>2015 | <span style="border: 1px solid grey; padding: 0 5px">16+</span> | 共 3 季 | 犯罪電視戲劇</span>
         <p>本片評分：</p><b-form-rating
          v-model="value2"
          variant="danger"
          readonly
          show-value
          show-value-max
          precision="1">
          </b-form-rating>
          <el-tabs v-model="activeName" type="card">
            <el-tab-pane label="簡介" name="first">潔西卡過去的創傷一直糾纏不休，但她用她身為私家偵探的天賦，來找到她的折磨者，阻止他不會傷害地獄廚房裡的其他人。</el-tab-pane>
            <el-tab-pane label="主演" name="second">克里斯汀·里特 （Krysten Ritter）戴維·肯尼斯（David Kennes），瑞秋·泰勒（Rachael Taylor），邁克·科特（Mike Colter），凱莉·安妮·莫斯（Carrie-Anne Moss），埃卡·達爾維爾（Eka Darville），艾琳·莫里亞蒂（Erin Moriarty），威爾·特拉瓦爾（Wil Traval），蘇西·阿布羅梅特（Susie Abromeit）</el-tab-pane>
          </el-tabs>
      </div>
    </div>
    <div class="wrap">
      <h1>各集簡介</h1>
        <b-tabs content-class="mt-3" small>
            <b-tab title="第一季" active>
              <div class="content">
                <div class="image" v-for="(img, index) in imgs" :key="index">
                  <img :src="img.img">
                  <p>第{{img.id}}集</p>
                  <span>{{img.summary}}</span>
                </div> 
              </div>
            </b-tab>
            <b-tab title="第二季">
              <div class="content">
                <div class="image" v-for="(img, index) in imgs2" :key="index">
                  <img :src="img.img">
                  <p>第{{img.id}}集</p>
                  <span>{{img.summary}}</span>
                </div> 
              </div>
            </b-tab>
            <b-tab title="第三季">
              <div class="content">
                <div class="image" v-for="(img, index) in imgs3" :key="index">
                  <img :src="img.img">
                  <p>第{{img.id}}集</p>
                  <span>{{img.summary}}</span>
                </div> 
              </div>
            </b-tab>
        </b-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      value2: 3.5,
      activeName: 'first',
      imgs: [],
      imgs2: [],
      imgs3: []
    }
  },
  created(){
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            console.log(res.data);
            this.imgs = res.data.jessica;
        })
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs2 = res.data.jessica2;
        })
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs3 = res.data.jessica3;
        })
    }
  
}
</script>

<style scoped>
*{
  color: #FFF;
}
.top{
  width: 100%;
  padding: 3% 10%;
  display: flex;
  margin: 0 auto;
  background: linear-gradient(to right, #000,rgba(0,0,0,0.4)),url('https://www.screengeek.net/wp-content/uploads/2017/04/jessica-jones.jpg') no-repeat scroll right;
  background-size: contain;
}
.left{
  flex-basis: 40%;
}
.left img{
  width: 400px;
}
.right{
  flex-basis: 60%;
}
.right img{
  width: 60%;
  margin-bottom: 2rem;
}
.right span{
  font-size: 0.9rem;
  letter-spacing: 1px;
  line-height: 2rem;
  color: grey;
}
.right p{
  line-height: 2rem;
}
.b-rating{
  width: 31%;
  color: #000;
  margin-bottom: 3rem;
}
.el-tabs{
  width: 70%;
}

.wrap{
  width: 100%;
  background: rgb(22, 22, 22);
  padding: 2rem 3rem;
}

.content{
  width: 100%;
  padding: 1rem 0;
  margin: 0 auto;

}
.image{
  width: 22.5%;
  min-height: 320px;
  display: inline-block;
  margin: 1rem 1rem 0 1rem;
  vertical-align: super;
}
.image img{
  width: 100%;
  margin-bottom: 1rem;
}
.image span{
  color: #FFF;
  font-size: 0.7rem;
}

@media only screen and (max-width: 450px){
.top{
  width: 100%;
  padding: 0 5%;
  flex-direction: column;
  background: #000;
}
.left{
  flex-basis: 90%;
  margin-bottom: 2rem;
}
.left img{
  width: 100%;
}
.right{
  margin-left: 2rem;
  padding-bottom: 3rem;
}
.right img{
  display: none;
}
.b-rating{
  width: 70%;
}
.el-tabs{
  width: 90%;
}
.wrap{
  padding: 2rem 1rem;
}
.image{
  width: 100%;
  margin: 2rem auto;
}
.content h1, .content h3{
  margin-left: 0;
}
}
</style>


