<template>
  <div>
    <div class="top">
      <div class="left">
        <img src="https://netflixrank.com/public/upload/550/1597823911Bodyguard.jpg">
      </div>
      <div class="right">
         <img src="https://occ-0-3594-2219.1.nflxso.net/dnm/api/v6/tx1O544a9T7n8Z_G12qaboulQQE/AAAABQ9CghjG_fA8foOVrVwcW6GFU3Q_JdWeqIcIRyk3brNp3fVBHmyG1_P99GY6rFHK1X9IFIWITjVDgeMfUdKvSCo_YvzQcdCByFROyCX3eXtIVBvFcd90weCVlRZz2TUazcSzuw1aSoDzrKfIpI-gQpfvowS94H4BqnuFq04HbmiGMA.png?r=6e7">
         <h2>內政保鑣</h2>
         <span>2018 | <span style="border: 1px solid grey; padding: 0 5px">16+</span> | 1 季 | 驚悚節目</span>
         <p>本片評分：</p><b-form-rating
          v-model="value2"
          variant="danger"
          readonly
          show-value
          show-value-max
          precision="1">
          </b-form-rating>
          <el-tabs v-model="activeName" type="card">
            <el-tab-pane label="簡介" name="first">協助阻撓一起恐怖攻擊後，這名曾經馳騁沙戰的退伍軍人被指派保護一位政治人物，但受保護的對象恰是推動那場為他帶來陰影的戰役的主要人物。</el-tab-pane>
            <el-tab-pane label="主演" name="second">理查德·麥登（Richard Madden），基利·霍斯（Keeley Hawes），吉娜·麥基（Gina McKee），索菲·倫德（Sophie Rundle），文森特·富蘭克林（Pinpa Haywood），保羅·雷迪（Paul Ready），湯姆·布魯克（Tom Brooke），尼古拉斯·格里夫斯（Nicholas Gleaves），斯圖爾特·鮑曼（Stuart Bowman）</el-tab-pane>
          </el-tabs>
      </div>
    </div>
    <div class="wrap">
      <h1>各集簡介</h1>
        <b-tabs content-class="mt-3" small>
            <b-tab title="第一季" active>
              <div class="content">
                <div class="image" v-for="(img, index) in imgs" :key="index">
                  <img :src="img.img">
                  <p>第{{img.id}}集</p>
                  <span>{{img.summary}}</span>
                </div> 
              </div>
            </b-tab>
        </b-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      value2: 4,
      activeName: 'first',
      imgs: []
    }
  },
  created(){
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs = res.data.bodyguard;
        })
    }
  
}
</script>

<style scoped>
*{
  color: #FFF;
}
.top{
  width: 100%;
  padding: 3% 10%;
  display: flex;
  margin: 0 auto;
  background: linear-gradient(to right, #000,rgba(0,0,0,0.6)),url('https://pic.pimg.tw/catsirena/1542070224-3249761366_n.png') no-repeat scroll right;
  background-size: contain;
}
.left{
  flex-basis: 40%;
}
.left img{
  width: 400px;
}
.right{
  flex-basis: 60%;
}
.right img{
  width: 60%;
  margin-bottom: 2rem;
}
.right span{
  font-size: 0.9rem;
  letter-spacing: 1px;
  line-height: 2rem;
  color: grey;
}
.right p{
  line-height: 2rem;
}
.b-rating{
  width: 31%;
  color: #000;
  margin-bottom: 3rem;
}
.el-tabs{
  width: 70%;
}

.wrap{
  width: 100%;
  background: rgb(22, 22, 22);
  padding: 2rem 3rem;
}

.content{
  width: 100%;
  padding: 1rem 0;
  margin: 0 auto;

}
.image{
  width: 22.5%;
  min-height: 320px;
  display: inline-block;
  margin: 1rem 1rem 0 1rem;
  vertical-align: super;
}
.image img{
  width: 100%;
  margin-bottom: 1rem;
}
.image span{
  color: #FFF;
  font-size: 0.7rem;
}

@media only screen and (max-width: 450px){
.top{
  width: 100%;
  padding: 0 5%;
  flex-direction: column;
  background: #000;
}
.left{
  flex-basis: 90%;
  margin-bottom: 2rem;
}
.left img{
  width: 100%;
}
.right{
  margin-left: 2rem;
  padding-bottom: 3rem;
}
.right img{
  display: none;
}
.b-rating{
  width: 70%;
}
.el-tabs{
  width: 90%;
}
.wrap{
  padding: 2rem 1rem;
}
.image{
  width: 100%;
  margin: 2rem auto;
}
.content h1, .content h3{
  margin-left: 0;
}
}
</style>


