<template>
  <div>
    <div class="top">
      <div class="left">
        <img src="https://www.dramaqueen.com.tw/images/video_poster/2020/202003251800093344070e86fb158c2e5ac94a3fcd0cad9b52.jpg">
      </div>
      <div class="right">
         <img src="https://occ-0-3594-2219.1.nflxso.net/dnm/api/v6/tx1O544a9T7n8Z_G12qaboulQQE/AAAABVmbyvQ7r6I-_yxtIom_l8w5Rx1DeqFO6xxVL-R0WGmXoUePAOQX27tDOfntMLsXi7PzzsSMLqgj0mUEBXpTlrQ3ytPwRHxrM8_oxX5lvI3riXEl_Re8xKtmFB22USpXPxXfir4Lt5F9Y4WHQtif451qhk5ir0Hjz-qk7rqEvox6GQ.png?r=127">
         <h2>紙房子</h2>
         <span>2017 | <span style="border: 1px solid grey; padding: 0 5px">16+</span> | 共 4 季 | 驚悚節目</span>
         <p>本片評分：</p><b-form-rating
          v-model="value2"
          variant="danger"
          readonly
          show-value
          show-value-max
          precision="1">
          </b-form-rating>
          <el-tabs v-model="activeName" type="card">
            <el-tab-pane label="簡介" name="first">八名竊賊挾持人質，並將自己反鎖在西班牙皇家造幣廠內，由一名犯罪首腦在執行計畫期間，負責操縱警方。</el-tab-pane>
            <el-tab-pane label="主演" name="second">烏蘇拉·可貝蘿，艾瓦羅·莫特，伊特斯爾·伊圖諾</el-tab-pane>
          </el-tabs>
      </div>
    </div>
    <div class="wrap">
      <h1>各集簡介</h1>
      <b-tabs content-class="mt-3" small>
        <b-tab title="第一季" active>
          <div class="content">
            <div class="image" v-for="(img, index) in imgs" :key="index">
              <img :src="img.img">
              <p>第{{img.id}}集</p>
              <span>{{img.summary}}</span>
            </div> 
          </div>
        </b-tab>
        <b-tab title="第二季">
           <div class="content">
            <div class="image" v-for="(img, index) in imgs2" :key="index">
              <img :src="img.img">
              <p>第{{img.id}}集</p>
              <span>{{img.summary}}</span>
            </div> 
          </div>
        </b-tab>
        <b-tab title="第三季">
          <div class="content">
            <div class="image" v-for="(img, index) in imgs3" :key="index">
              <img :src="img.img">
              <p>第{{img.id}}集</p>
              <span>{{img.summary}}</span>
            </div> 
          </div>
        </b-tab>
        <b-tab title="第四季">
          <div class="content">
            <div class="image" v-for="(img, index) in imgs4" :key="index">
              <img :src="img.img">
              <p>第{{img.id}}集</p>
              <span>{{img.summary}}</span>
            </div> 
          </div>
        </b-tab>
    </b-tabs>
      
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      value2: 4.5,
      activeName: 'first',
      imgs: [],
      imgs2: [],
      imgs3: [],
      imgs4: [],
    }
  },
  created(){
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs = res.data.moneys1;
        })
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs2 = res.data.moneys2;
        })
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs3 = res.data.moneys3;
        })
        this.$ajax.get('https://kcl9503.github.io/axios/db.json')
        .then(res => {
            this.imgs4 = res.data.moneys4;
        })
    }
  
}
</script>

<style scoped>
*{
  color: #FFF;
}
.top{
  width: 100%;
  padding: 3% 10%;
  display: flex;
  margin: 0 auto;
  background: linear-gradient(to right, #000,rgba(0,0,0,0.8)),url('https://womany.net/cdn-cgi/image/w=800,fit=scale-down,f=auto/https://lh4.googleusercontent.com/t1-gkj4tKasVY1aCmcHf4Omit1Eemj95WuWgDGVcSsLw2OKT7VBYz_OwMHAGrh1er906eZWqZPj7gtBsOL4STSpRragslt8RfJrO0ke3jA7aZ4sT0gcATLfw7O8mj_BhNNqRdJUe') no-repeat scroll right;
  background-size: contain;
}
.left{
  flex-basis: 40%;
}
.left img{
  width: 400px;
}
.right{
  flex-basis: 60%;
}
.right img{
  width: 60%;
  margin-bottom: 2rem;
}
.right span{
  font-size: 0.9rem;
  letter-spacing: 1px;
  line-height: 2rem;
  color: grey;
}
.right p{
  line-height: 2rem;
}
.b-rating{
  width: 31%;
  color: #000;
  margin-bottom: 3rem;
}
.el-tabs{
  width: 70%;
}

.wrap{
  width: 100%;
  background: rgb(22, 22, 22);
  padding: 2rem 3rem;
}

.content{
  width: 100%;
  padding: 1rem 0;
  margin: 0 auto;

}
.image{
  width: 22.5%;
  min-height: 320px;
  display: inline-block;
  margin: 1rem 1rem 0 1rem;
  vertical-align: super;
}
.image img{
  width: 100%;
  margin-bottom: 1rem;
}
.image span{
  color: #FFF;
  font-size: 0.8rem;
}

@media only screen and (max-width: 450px){
.top{
  width: 100%;
  padding: 0 5%;
  flex-direction: column;
}
.left{
  flex-basis: 90%;
  margin-bottom: 2rem;
}
.left img{
  width: 100%;
}
.right{
  margin-left: 2rem;
  padding-bottom: 3rem;
}
.right img{
  display: none;
}
.b-rating{
  width: 70%;
}
.el-tabs{
  width: 90%;
}
.wrap{
  padding: 2rem 1rem;
}
.image{
  width: 100%;
  margin: 2rem auto;
}
.content h1, .content h3{
  margin-left: 0;
}
}
</style>


