import Vue from 'vue';
import Router from 'vue-router';
import Home from '../components/home.vue';
import Money from '../components/money.vue';
import Paris from '../components/paris.vue';
import Luke from '../components/luke.vue';
import Guard from '../components/guard.vue';
import If from '../components/if.vue';
import Jessica from '../components/jessica.vue';

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', redirect: {name:'home'}},
    {name: 'home', path:'/home', component: Home, meta: {title: 'J電視劇介紹'}},
    {name: 'paris', path:'/paris', component: Paris, meta: {title: '艾蜜莉在巴黎'}},
    {name: 'money', path:'/money', component: Money, meta: {title: '紙房子'}},
    {name: 'luke', path:'/luke', component: Luke, meta: {title: '曼威盧克凱奇'}},
    {name: 'guard', path:'/guard', component: Guard, meta: {title: '內政保鑣'}},
    {name: 'if', path:'/if', component: If, meta: {title: '如/果'}},
    {name: 'jessica', path:'/jessica', component: Jessica, meta: {title: '曼威潔西卡瓊斯'}},


  ],
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
     return savedPosition
    } else {
     return { x: 0, y: 0 }
    }
  }


})


